"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

// Mock data for demonstration
const MOCK_DESIGNS = [
  {
    id: "1",
    name: "Classic Suit",
    imageUrl: "https://images.pexels.com/photos/3755706/pexels-photo-3755706.jpeg",
  },
  {
    id: "2",
    name: "Traditional Kaftan",
    imageUrl: "https://images.pexels.com/photos/5875803/pexels-photo-5875803.jpeg",
  },
  {
    id: "3",
    name: "Business Shirt",
    imageUrl: "https://images.pexels.com/photos/297933/pexels-photo-297933.jpeg",
  },
]

export default function VirtualFitting() {
  const [selectedDesign, setSelectedDesign] = useState<string>("")
  const [isLoading, setIsLoading] = useState(false)
  const [showPreview, setShowPreview] = useState(false)

  const handleStartFitting = async () => {
    if (!selectedDesign) return

    setIsLoading(true)
    // Simulate loading time for virtual fitting process
    await new Promise(resolve => setTimeout(resolve, 2000))
    setIsLoading(false)
    setShowPreview(true)
  }

  const selectedDesignData = MOCK_DESIGNS.find(d => d.id === selectedDesign)

  return (
    <div className="max-w-4xl mx-auto">
      <h1 className="text-3xl font-bold font-poppins mb-6">
        Virtual Fitting Room
      </h1>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">Try On Clothes Virtually</h2>
          <div className="space-y-6">
            <div>
              <label className="block text-sm font-medium mb-2">
                Select Design
              </label>
              <Select
                value={selectedDesign}
                onValueChange={setSelectedDesign}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Choose a design" />
                </SelectTrigger>
                <SelectContent>
                  {MOCK_DESIGNS.map((design) => (
                    <SelectItem key={design.id} value={design.id}>
                      {design.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <Button
              onClick={handleStartFitting}
              disabled={!selectedDesign || isLoading}
              className="w-full bg-black text-white hover:bg-gray-900"
            >
              {isLoading ? "Processing..." : "Start Virtual Fitting"}
            </Button>

            <div className="text-sm text-gray-500">
              <h3 className="font-medium mb-2">How it works:</h3>
              <ol className="list-decimal list-inside space-y-1">
                <li>Select the design you want to try on</li>
                <li>Our AI will create a virtual model based on your measurements</li>
                <li>See how the clothes will look on you</li>
                <li>Adjust and customize as needed</li>
              </ol>
            </div>
          </div>
        </Card>

        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">Preview</h2>
          {showPreview && selectedDesignData ? (
            <div className="space-y-4">
              <div className="aspect-square relative bg-gray-100 rounded-lg overflow-hidden">
                <img
                  src={selectedDesignData.imageUrl}
                  alt={selectedDesignData.name}
                  className="object-cover w-full h-full"
                />
              </div>
              <div className="text-center">
                <p className="text-sm text-gray-500 mb-4">
                  This is a preview of how {selectedDesignData.name} might look based on your measurements.
                  In the future, this will be replaced with an actual 3D model.
                </p>
                <Button
                  variant="outline"
                  onClick={() => setShowPreview(false)}
                >
                  Try Another Design
                </Button>
              </div>
            </div>
          ) : (
            <div className="h-64 flex items-center justify-center text-gray-500 text-center">
              {isLoading ? (
                <div className="space-y-4">
                  <div className="animate-spin h-8 w-8 border-4 border-black border-t-transparent rounded-full mx-auto"></div>
                  <p>Generating virtual preview...</p>
                </div>
              ) : (
                <p>
                  Select a design and start the virtual fitting to see a preview
                </p>
              )}
            </div>
          )}
        </Card>
      </div>

      <Card className="mt-8 p-6 bg-gray-50">
        <h2 className="text-xl font-semibold mb-4">Coming Soon</h2>
        <p className="text-gray-600">
          We're working on advanced 3D modeling features that will provide an even more realistic virtual fitting experience:
        </p>
        <ul className="list-disc list-inside mt-4 space-y-2 text-gray-600">
          <li>Real-time 3D visualization</li>
          <li>360-degree view of fitted clothes</li>
          <li>Fabric simulation and movement</li>
          <li>Custom color and pattern options</li>
        </ul>
      </Card>
    </div>
  )
}
